import React from 'react';
import Slide from "./slide";

function IntroContent(props) {
    return (
        <section className='d-flex justify-content-center align-items-center flex-column'
                 style={{height: '100vh', color: 'white'}}>
            <h1>{props.name}</h1>
            <i className='animateName mt-4'>{props.specialization}</i>
        </section>
    )
}

function IntroSlide(props) {
    return (
        <section className='col-12 d-flex align-items-center flex-wrap' style={{height: '100vh'}}>
            <div className='col-lg-6 col-sm-12 infoSection d-flex justify-content-center'>
                <div className='infoContainer'>
                    <div className='profilePic mt-4'>
                        <img src={props.data.src}/>
                    </div>
                    <div className='mt-3'>
                        <h4>Pavan Kumar Divvela</h4>
                        <p>Senior Software Engineer</p>
                    </div>
                    <div className='p-4'>
                        <hr/>
                        <div className='d-flex justify-content-between'>
                            <img src='images/undraw_mobile_prle.svg' style={{width: '12px'}}/>
                            <span>&nbsp;(+91) 9036057988</span>
                        </div>
                        <hr/>
                        <div className='d-flex justify-content-between'>
                            <img src='images/mail.svg' style={{width: '16px'}}/>
                            <span>&nbsp;pavankumar1014@gmail.com</span>
                        </div>
                        <hr/>
                        <div className='d-flex justify-content-between'>
                            <img src='images/social.svg' style={{width: '20px'}}/>
                            <span>&nbsp;www.linkedin.com/in/pavandivvela</span>
                        </div>
                        <hr/>
                        <div className='d-flex justify-content-between'>
                            <img src='images/location.svg' style={{width: '20px'}}/>
                            <span>&nbsp;Bangalore</span>
                        </div>
                    </div>
                </div>
            </div>
            <div className='col-lg-6 col-sm-12'>
                <section className='d-flex justify-content-center align-items-center flex-column'
                         style={{height: '100vh', color: 'white'}}>
                    <i className='animateName mt-4'>{props.data.specialization}</i>
                </section>
            </div>
        </section>

    )
}

export default IntroSlide;