import React from 'react';

function Slide(props) {
    return (
        <section className='row' style={{height: '100vh'}}>
            {props.imageAlign === 'left' ? <div
                className={'d-flex justify-content-center ' + (props.responsive ? 'col-lg-6 col-sm-12 align-items-center' : "col-6")}>
                <img src={props.image} className='img-fluid'/></div> : ''}
            <article className={props.responsive ? 'col-lg-6 col-sm-12 align-items-center' : "col-6"}>
                {typeof props.content === 'function' ? props.content() : props.content}
            </article>
            {props.imageAlign === 'right' ?
                <div className='col-6 d-flex justify-content-center'><img src={props.image} className='img-fluid'/>
                </div> : ''}
        </section>
    )
}

export default Slide;